import React, { useState } from "react"

import "./Join.css"

const Join = ({ room, setRoom }) => {
    const [tempRoom, setTempRoom] = useState("")

    const roomHandler = (e) => {
        setTempRoom(e.target.value)
    }

    const handleJoin = () => {
        if(tempRoom){
            setRoom(tempRoom)
        } else {
            setRoom("Room 1")
        }
    }

    return (
        <div className="container mt-6 mb-6">
            <div className="box">
                <h1 className="title is-1">Join</h1>
                <div className="field">
                    <label className="label">Room</label>
                    <div className="control has-icons-left">
                        <div className="select">
                            <select onChange={roomHandler}>
                                <option value="Room 1">Room 1</option>
                                <option value="Room 2">Room 2</option>
                                <option value="Room 3">Room 3</option>
                                <option value="Room 4">Room 4</option>
                                <option value="Room 5">Room 5</option>
                            </select>
                        </div>
                        <span className="icon is-left">
                            <i className="fa fa-door-open"></i>
                        </span>
                    </div>
                </div>
                <button onClick={handleJoin} className="button is-success" type="submit">Join</button>
            </div>
        </div>
    )
}

export default Join