import React from "react"

const notFound = ({ history }) => {

    const clickHandler = () => {
        history.push("/")
    }

    return(
        <div className="container">
            <div className="box mt-6 mb-6">
                <h1 className="title has-text-centered is-family-code" style={{fontSize: 10 + "rem"}}>404</h1>
                <h1 className="title is-1 has-text-centered">Page Not Found</h1>
                <div className="field">
                    <div className="buttons is-centered">
                        <button onClick={clickHandler} className="button is-info">Back To Homepage</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default notFound