import React from "react"
import ReCAPTCHA from "react-google-recaptcha"

const RegisterForm = ({ setUsername, setEmail, setPass, Register, setRecaptchaToken }) => {

    const recaptchaChange = (e) => {
        setRecaptchaToken(e)
    }

    return (
        <div className="container mt-6 mb-6">
            <div className="box">
                <h1 className="title is-1">Register</h1>
                <div className="field">
                    <label className="label">Username</label>
                    <div className="control has-icons-left">
                        <input onChange={e => setUsername( e.target.value )} type="text" placeholder="Username" className="input" />
                        <span className="icon is-small is-left">
                            <i className="fas fa-user"></i>
                        </span>
                    </div>
                </div>
                <div className="field">
                    <label className="label">Email</label>
                    <div className="control has-icons-left">
                        <input onChange={e => setEmail( e.target.value )} type="email" placeholder="Email" className="input" />
                        <span className="icon is-small is-left">
                            <i className="fas fa-envelope"></i>
                        </span>
                    </div>
                </div>
                <div className="field">
                    <label className="label">Password</label>
                    <div className="control has-icons-left">
                        <input onChange={e => setPass( e.target.value )} type="password" placeholder="Password" className="input" />
                        <span className="icon is-small is-left">
                            <i className="fas fa-lock"></i>
                        </span>
                    </div>
                </div>
                <ReCAPTCHA
                    sitekey="6LejJMwZAAAAAPogD7VPC5IFCv_d3AJLXlGLHOd9"
                    //sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"
                    onChange={recaptchaChange}
                    className="mb-1"
                />
                <button onClick={Register} className="button is-success">Register</button>
            </div>
        </div>
    )
}

export default RegisterForm