import React, { useState, useEffect } from "react"
import { Redirect } from "react-router-dom"
import Cookie from "js-cookie"
import io from "socket.io-client"
import Swal from "sweetalert2"

let socket

const Account = ({ history }) => {

    let [userData, setUserData] = useState({})

    const [usernameChange, setUsernameChange] = useState("")
    const [emailChange, setEmailChange] = useState("")
    const [disabledBtn, setDisabledBtn] = useState()

    let ENDPOINT = "https://shorchatserver.herokuapp.com/"
    //let ENDPOINT = "http://localhost:5000"

    const logoutHandler = () => {
        Cookie.remove("token")
        Cookie.remove("name")
        history.push("/")
    }

    const deleteHandler = () => {
        Swal.fire({
            icon: "warning",
            title: "Are you sure?",
            html: "Are you sure you want to <b>PERMANENTLY DELETE</b> your account?",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: '#d33',
            cancelButtonColor: '#05A600',
        }).then((result) => {
            if(result.isConfirmed){
                socket.emit("delete-account", Cookie.get("token"))
            }
        })
    }

    const saveHandler = () => {
        console.log("New Name: " + usernameChange + "\nNew Email: " + emailChange)

        let username = usernameChange || userData.name
        let email = emailChange || userData.email

        socket.emit("edit-account", { token: Cookie.get("token"), name: username, email: email })
    }
    useEffect(() => {
        setDisabledBtn(false)
    }, [usernameChange, emailChange])

    useEffect(() => {
        setDisabledBtn(true)
        socket = io(ENDPOINT)

        socket.emit("get-account-data", Cookie.get("token"))

        socket.on("account-data", data => {
            setUserData(data)
        })

        socket.on("confirm-delete", data => {
            Swal.fire({
                icon: "info",
                title: "Confirmation",
                html: `Confirmation token has been sent to <b>${data.email}</b>`,
                input: "text",
                showCancelButton: true,
                confirmButtonText: "Yes",
                confirmButtonColor: '#d33',
                cancelButtonColor: '#05A600',
            }).then((result) => {
                if(result.value === data.confToken){
                    socket.emit("delete-account-confirmed", Cookie.get("token"))
                } else if(result.value) {
                    Swal.fire({
                        icon: "error",
                        title: "Error",
                        text: "Invalid token!"
                    })
                }
            })
        })
        socket.on("account-deleted", () => {
            Swal.fire({
                icon: "success",
                title: "Deleted!",
                text: "Your account has been successfully deleted!"
            })
            logoutHandler()
        })

        socket.on("successfully-edited", () => {
            setDisabledBtn(true)

            Swal.fire({
                icon: "success",
                title: "Edited!",
                text: "Successfully edited account information!"
            })
        })

        return () => {
            socket.off()
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [ENDPOINT])

    return(
        <div className="container mt-6 mb-6">
            {(userData !== null) ? (
                <div className="box">
                <h1 className="title is-1">Account Information</h1>
                <div className="block">
                    <div className="title">Username:</div>
                    <div className="box">
                        <div className="control has-icons-left">
                            <input className="input is-medium" defaultValue={userData.name} onChange={e => setUsernameChange(e.target.value)} />
                            <span className="icon is-lg is-left">
                                <i className="fas fa-edit"></i>
                            </span>
                        </div>
                    </div>
                </div>
                <div className="block">
                    <div className="title">Email:</div>
                    <div className="box">
                        <div className="control has-icons-left">
                            <input className="input is-medium" defaultValue={userData.email} onChange={e => setEmailChange(e.target.value)} />
                            <span className="icon is-lg is-left">
                                <i className="fas fa-edit"></i>
                            </span>
                        </div>
                    </div>
                </div>
                <div className="block">
                    <div className="title">Date Created:</div>
                    <div className="box">
                        <h4 className="subtitle is-4">{userData.dateCreated}</h4>
                    </div>
                </div>
                <div className="block">
                    <div className="title">ID:</div>
                    <div className="box">
                        <h4 className="subtitle is-4"> {userData.id}</h4>
                    </div>
                </div>
                <div className="buttons">
                    <button onClick={saveHandler} className="button is-success" disabled={disabledBtn} >Save</button>
                    <button onClick={logoutHandler} className="button is-danger">Log out</button>
                    <button onClick={deleteHandler} className="button is-danger">Delete Account</button>
                </div>
            </div>
            ): (
                <Redirect to="/" />
            )}
        </div>
    )
}

export default Account