import React, { useState, useEffect } from "react"
import io from "socket.io-client"
import axios from "axios"

let socket

const Users = () => {
    const [users, setUsers] = useState([])
    const [loading, setLoading] = useState(true)

    let ENDPOINT = "https://shorchatserver.herokuapp.com/"

    useEffect(() => {
        socket = io(ENDPOINT)

        axios.get(ENDPOINT + "users").then(result => {
            setTimeout(() => {
                setLoading(false)
                setUsers(result.data)
            }, 1400)
        })

        return () => {
            socket.off()
        }
    }, [ENDPOINT])

    return(
        <div className="container">
            <div className="box mt-6 mb-6">
                <h1 className="title is-1">All Users:</h1>
                {loading === false ? (
                    <h2 className="subtitle is-3">Total Users: <b>{users.length}</b></h2>
                ): null}
                {loading === true ? (
                    <progress className="progress is-info is-large" max="100"></progress>
                ): null}
                {(users) ? (
                    users.map((user, i) => 
                        <div key={i} className="box">
                            <h4 className="subtitle is-4">{user.name} {user.awards.map(award => <i className={award[1] + " mr-2"} title={award[0]}></i>)}</h4>
                        </div>
                    )
                ): null}
            </div>
        </div>
    )
}

export default Users