import React, { useState, useEffect } from "react"
import io from "socket.io-client"
import Cookie from "js-cookie"

import "./Chat.css"

import Input from "../Input/Input"
import Messages from "../Messages/Messages"
import UsersOnline from "../UsersOnline/UsersOnline"
import Join from "../Join/Join"

let socket

const Chat = ({ location }) => {
    const [name, setName] = useState("")
    const [room, setRoom] = useState("")
    const [users, setUsers] = useState("")
    const [message, setMessage] = useState([])
    const [messages, setMessages] = useState([])
    const [ENDPOINT, setENDPOINT] = useEffect("")
    //const ENDPOINT = "http://localhost:5000"

    useEffect(() => {
        socket = io(ENDPOINT)
        
        setName(Cookie.get("name"))

        socket.emit("join", { name, room }, (error) => {
            if(error){
                alert(error)
                document.location = "/"
            }
        })

        socket.on("message", message => {
            setMessages([...messages, message])
        })
        socket.on("roomData", ({users}) => {
            setUsers(users)
        })

        return () => {
            socket.emit("disconnect")

            socket.off()
        }
        // eslint-disable-next-line
    }, [ENDPOINT, name, room])
    useEffect(() => {
        setENDPOINT("https://shorchatserver.herokuapp.com/")
        // eslint-disable-next-line
    }, [room])

    useEffect(() => {
        socket.on("message", message => {
            setMessages([...messages, message])
        })

        socket.on("roomData", ({users}) => {
            setUsers(users)
        })
    }, [messages])

    const sendMessage = (e) => {
        if(!message || message === undefined || message === null) return alert("You cannot send blank messages!")

        e.preventDefault()

        socket.emit("sendMessage", message, () => setMessage(""))
    }

    return (
        <div>
            {(room && name) ? (
            <div className="chatContainer">
                <div>
                    <div className="UsersOnline">
                        <UsersOnline users={users} room={room} />
                    </div>
                    <button className="button is-danger leaveButton">Leave</button>
                    <div className="container messagesContainer">
                        <div>
                            <Messages messages={messages} name={name} />
                        </div>
                        <Input message={message} setMessage={setMessage} sendMessage={sendMessage} />
                    </div>
                </div>
            </div>
            ): (
                <Join room={room} setRoom={setRoom} />
            )}
        </div>
    )
}

export default Chat