import React from 'react';

import "./UsersOnline.css"

const UsersOnline = ({ users, room }) => (
  <div className="usersContainer">
    <h1 className="notification title is-4"><b>Users In {room}:</b></h1>
    {
      users
        ? (
          <div>
            <div className="activeContainer">
              <h2>
                {users.map(({name}) => (
                  <div className="notification subtitle is-4" key={name}>
                    {name}
                  </div>
                ))}
              </h2>
            </div>
          </div>
        )
      : null
    }
  </div>
);

export default UsersOnline;